import { cva } from 'class-variance-authority';
import { SpacingProps } from './spacing.interface';

export const spacing = cva('zep-flex', {
  variants: {
    direction: {
      row: 'zep-flex-row',
      column: 'zep-flex-col',
    },
    gap: {
      '0': '',
      '0.25': 'zep-gap-0.25',
      '0.5': 'zep-gap-0.5',
      '0.75': 'zep-gap-0.75',
      '1': 'zep-gap-1',
      '1.5': 'zep-gap-1.5',
      '2': 'zep-gap-2',
      '2.5': 'zep-gap-2.5',
      '3': 'zep-gap-3',
      '3.75': 'zep-gap-3.75',
      '4': 'zep-gap-4',
    },
    justifyContent: {
      start: 'zep-justify-start',
      end: 'zep-justify-end',
      center: 'zep-justify-center',
      between: 'zep-justify-between',
      around: 'zep-justify-around',
      evenly: 'zep-justify-evenly',
    },
    alignItems: {
      start: 'zep-items-start',
      end: 'zep-items-end',
      center: 'zep-items-center',
      baseline: 'zep-items-baseline',
      stretch: 'zep-items-stretch',
    },
    wrap: {
      wrap: 'zep-flex-wrap',
      nowrap: 'zep-flex-nowrap',
    },
  },
});

export const Spacing: React.FC<SpacingProps> = ({
  children,
  direction = 'row',
  gap = '0.25',
  justifyContent = 'start',
  alignItems = 'stretch',
  wrap = 'wrap',
  className,
}) => {
  return (
    <div
      className={`${spacing({ direction, gap, justifyContent, alignItems, wrap, className })} max-sm:[&>*]:zep-grow`}
      data-testid="zep-spacing"
    >
      {children}
    </div>
  );
};
